.Board {
  box-sizing: border-box;
  background-color: pink;
  width: 260px;
  text-align: center;
  padding: 20px;
  border-radius: 20px;
  font-size: 18px;
  background-color: #272b38;
  line-height: 24px;
}
.Board .board-title {
  margin: 20px auto 15px;
  color: #888d9d;
  font-weight: 400;
  font-size: 22px;
}
.winner {
  border: 1px solid #fff;
  box-shadow: 0 0 12px 0 hsla(0, 0%, 100%, 0.48);
}
